import React from 'react';
import { graphql, Link, PageRendererProps, useStaticQuery } from 'gatsby';
import styled from 'styled-components';

const StyledHeader = styled.header`
  & {
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #fff;

    & > div {
      /* max-width: 1024px; */
      max-width: 680px;
    }
  }
`;

interface Props {
  location: PageRendererProps['location'];
}

const Header: React.FC<Props> = ({ location }) => {
  const { site } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          social {
            instagram
          }
        }
      }
    }
  `);
  const isRootPath =
    location.pathname === '/blog' || location.pathname === '/blog/';
  return (
    <StyledHeader>
      <div
        className={`flex items-center justify-between mx-auto ${
          isRootPath ? 'py-5' : 'py-4'
        }`}
      >
        <div>
          <Link to="/blog">
            <img
              style={{
                width: isRootPath ? 239 : 93,
                height: isRootPath ? 54 : 21,
              }}
              src="/logo.png"
              alt="logo"
            />
          </Link>
        </div>
        <div>
          <a
            href={site.siteMetadata.social.instagram}
            target="_blank"
            rel="noreferrer noopener"
          >
            <i className="bx bxl-instagram text-gray-500 text-2xl" />
          </a>
        </div>
      </div>
    </StyledHeader>
  );
};

export default Header;
